import React from 'react';
import './App.css';
import {GameBoard} from "./components/GameBoard";
import {LeaderBoard} from "./components/LeaderBoard";

class App extends React.Component {
  constructor(props){
    super(props);


    this.count = 0


    this.state = {
        indexes: Array.from(Array(5),
            (_, i) => Array.from(Array(5),
                (_, k) => [i, k])).flat().sort(this.shuffle),
        dots: Array.from(Array(5), () => Array.from(Array(5), () => null)),
        newGame: false,
        computer: 0,
        user: 0,
        winner: '',
        mode: '0',
        name: '',
        gameSettings: [],
        dashboardData: []
    };


    this.shuffle = this.shuffle.bind(this)
    this.blueBg = this.blueBg.bind(this);
    this.userClick = this.userClick.bind(this);
    this.newGame = this.newGame.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }


  shuffle(){
      return Math.random() - 0.5;
  }

  blueBg(){

      if(this.state.name.length > 0 && this.state.mode != '0') {
        let dot = this.state.dots.slice();
        let gameLength = dot.length * dot.length

        /* game over */
        if (this.state.computer >= dot.length**2 / 2 || this.state.user  >= dot.length**2 / 2 ){
            this.count = 0;


            this.setState(state=>({
                winner: state.computer > state.user ? 'Computer' : state.name,
                newGame: !state.newGame,
                dashboardData: [
                    {
                        winner: state.computer > state.user ? 'Компьютер' : state.name,
                        date: `${new Date().toLocaleTimeString()}; ${new Date().toLocaleDateString()}`
                    }]
            }));
            (async()=>{
                let url = 'https://starnavi-frontend-test-task.herokuapp.com/winners'


                let respones = await fetch(url, {
                    method: 'POST', headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                    },
                    body: JSON.stringify(...this.state.dashboardData)
                })

                let result = await respones.json();
                console.log(result.message);

                this.getWinners()
            })()

            return

        }

          /*dot get a background*/
        const [x, y] = this.state.indexes[this.count];
        dot[x][y] = "blue";
        this.count++;

        this.setState({dots: dot})

        /*dot get a red background*/
        setTimeout(()=>{
            if(dot[x][y] === 'blue'){
                dot[x][y] = 'red'
                this.setState({
                    dots: dot,
                    computer: this.state.computer + 1
                })

                this.blueBg()
            }else if(dot[x][y] === 'green'){
                this.blueBg()
            }
        }, this.state.mode)
    }else{
        alert('Enter name and pick up game mode')
    }

  }

  newGame(){
      let dot = this.state.dots.slice();

      for(let x = 0; x < dot.length; x++){
          for(let y = 0; y < dot.length; y++){
              dot[x][y]= null
              this.setState({dots: dot})
          }
      }
      this.setState(state=>({
          newGame: !state.newGame,
          winner: '',
          mode: '0',
          name: ''
      }))
  }

  userClick(x,y){
      let dot = this.state.dots.slice();
      if(dot[x][y]==='blue'){
          dot[x][y] = 'green'
          this.setState(state=>({
              dots: dot,
              user: state.user + 1
          }))
      }
  }

  handleChange(e){
      let target = e.target
      let name = e.target.name
      if(name === 'mode'){
          if(target.value === "2000"){
              this.setState({
                  indexes: Array.from(Array(5),
                      (_, i) => Array.from(Array(5),
                          (_, k) => [i, k])).flat().sort(this.shuffle),
                  dots: Array.from(Array(5), () => Array.from(Array(5), () => null)),
              })
          }
          if(target.value === "1000"){
              this.setState({
                  indexes: Array.from(Array(10),
                  (_, i) => Array.from(Array(10),
                      (_, k) => [i, k])).flat().sort(this.shuffle),
                  dots: Array.from(Array(10), () => Array.from(Array(10), () => null)),
              })
          }
          if(target.value === "900"){
              this.setState({
                  indexes: Array.from(Array(15),
                      (_, i) => Array.from(Array(15),
                          (_, k) => [i, k])).flat().sort(this.shuffle),
                  dots: Array.from(Array(15), () => Array.from(Array(15), () => null)),
              })
          }

          this.setState({
              [name]: target.value,

          })
      }else{
          this.setState({[name]: target.value})
      }

  }

  getWinners() {
      (async()=>{
          let urlMode = 'https://starnavi-frontend-test-task.herokuapp.com/game-settings'
          let requestMode = await fetch(urlMode)
          let settings = await requestMode.json()

          let urlWinners = 'https://starnavi-frontend-test-task.herokuapp.com/winners'
          let requestWinner = await fetch(urlWinners)
          let dashboard = await  requestWinner.json()

          console.log(dashboard)

          let {easyMode, normalMode, hardMode} = settings

          this.setState({gameSettings: [
                  {easyMode: easyMode},
                  {normalMode: normalMode},
                  {hardMode: hardMode},
              ],
              dashboardData: [...dashboard]
          })

      })()
    }

  componentDidMount() {
      this.getWinners()
  }


    render(){

        console.log(this.state.dots.length**2 / 2)

        return (
            <div className={'main_wrap'}>
                <GameBoard
              winner={this.state.winner}
              dots={this.state.dots}
              blueBg={this.blueBg}
              newGame = {this.newGame}
              newGameFlag={this.state.newGame}
              winner = {this.state.winner}
              userClick = {this.userClick}
              mode = {this.state.mode}
              name = {this.state.name}
              handleChange={this.handleChange}
              settings = {this.state.gameSettings}
          />
                <LeaderBoard winnerObj={this.state.dashboardData}/>
            </div>
        )
    }

}

export default App;
