import React from "react";
import {Dot} from "./Dot";


export const GameBoard = ({winner, dots, blueBg, newGameFlag, newGame, userClick, name, mode, handleChange, settings}) => {


    return (
        <div className={'game_board'}>
            <div className="game_menu">
                <select
                    name="mode"
                    value={mode}
                    onChange={handleChange}
                >
                    <option value="0" hidden disabled selected>Pick game mode</option>
                    { settings.length > 0 && settings.map((mode, i)=>(
                        <option
                            key={i}
                            value={mode[Object.keys(mode)].delay}>
                            {Object.keys(mode)}
                        </option>
                    ))
                    }

                    {/*<option value={settings.easyMode}>easy</option>
                    <option value="500">normal</option>
                    <option value="300">hard</option>**/}
                </select>
                <input
                    type="text"
                    placeholder={'your name'}
                    name={'name'}
                    value={name}
                    onChange={handleChange}
                />
                <button onClick={newGameFlag ? newGame : blueBg}>
                    {newGameFlag ? 'New game' : 'play'}
                </button>
            </div>
            <div className="game">
                <p>{winner ?`Победил: ${winner}`  : 'Победитель'}</p>
                {dots.map((dots_row,x)=>{
                    return(
                        <div key={x} className={'dots_row'}>
                            {dots_row.map((dot,y)=>(
                                <Dot
                                    dot={dot}
                                    x={x}
                                    y={y}
                                    key={y}
                                    background={dots[x][y]}
                                    userClick = {userClick}
                                />
                            ))}
                        </div>
                    )
                })
                }
            </div>

        </div>
    )
}