import React from "react";


export const LeaderBoard = ({winnerObj}) => {
    return(
        <div className={'leaderBoard'}>
            <h1>Leader Board</h1>
                {winnerObj.length > 0 && winnerObj.map((winner, i)=>(
                    <div key={i}>
                        <p>
                            {winner.winner}
                        </p>
                        <p>
                            {winner.date}
                        </p>
                    </div>
                ))}


        </div>
    )
}